import React, { Fragment } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

function Room() {
  let { room_name } = useParams();
  const client = new WebSocket(`ws://127.0.0.1:8000/ws/chat/${room_name}/`);
  const out_put = React.useRef();
  const in_put = React.useRef();
  const [user, setUser] = React.useState({
    name: "",
  });

  //se ejecuta después de cada renderizado.
  React.useEffect(() => {
    client.onopen = () => {
      console.log("Connected to Django Services");
    };

    //get message
    client.onmessage = (Message) => {
      const data = JSON.parse(Message.data);
      out_put.current.value += `${data.user}: ${data.message} \n`;
    };

    //current user
    axios
      .get("http://127.0.0.1:8000/user/current_user/", {
        headers: {
          Authorization: `Token 0a24132e27534aa5d145898ab1e86b34b83a9058`,
        },
      })
      .then((res) => {
        const response = res.data;
        setUser({
          name: response.user,
        });
      });

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  //set message
  const sendMessage = () => {
    if (in_put.current.value) {
      client.send(
        JSON.stringify({
          message: in_put.current.value,
          user: user.name,
        })
      );
      in_put.current.value = "";
    }
  };

  //KeyCode === 13 is 'ENTER' on the keyboard
  const pressEnter = (key) => {
    if (key.keyCode === 13 && in_put.current.value) {
      sendMessage();
    }
  };

  return (
    <Fragment>
      <div className="row justify-content-center mt-5">
        <div className="col-md-4">
          <div className="form-floating">
            <textarea
              readOnly
              style={{ height: "320px", background: "#fff" }}
              className="form-control"
              id="floatingTextarea"
              ref={out_put}
            ></textarea>
            <label className="text-success" htmlFor="floatingTextarea">
              Chat: <b>{room_name}</b>
            </label>
          </div>

          <div className="input-group mb-3 mt-2">
            <input
              id="message-input"
              type="text"
              className="form-control"
              placeholder="Message..."
              ref={in_put}
              onKeyUp={pressEnter}
            />
            <input
              className="btn btn-outline-success"
              type="button"
              id="button-chat"
              onClick={sendMessage}
              value="Button"
            />
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default Room;
